import React from 'react'
import personService from '../services/person-service'

const PersonForm = ({persons, setPersons, newPerson, setNewPerson, setOutputMsg, clearOutputMsg}) => {

  const updatePerson = async (persons, newPerson) => {
    // confirm update with user
    const userConfirmedUpdate = window.confirm(`${newPerson.name} is already saved in contacts. Replace old number with new one?`)
    // stop if user cancels
    if (! userConfirmedUpdate) {
      return
    }
    const oldPerson = persons.find(p => p.name === newPerson.name)  // Grab old contact
    const personObj = { ...oldPerson, number: newPerson.number }  // create copy with updated number
    try {
      const updatedPerson = await personService.updateNumber(oldPerson.id, personObj)  // update contact in backend
      setPersons( persons.filter(p => p.id !== oldPerson.id).concat(updatedPerson) ) // set contacts by filtering out old person obj and concat new person obj
      setOutputMsg({message: `Contact ${newPerson.name} successfully updated.`, success: true})
      clearOutputMsg()
      setNewPerson({name: '', number: ''})  // reset input boxes
    } catch (err) {
      console.error(err)
      setOutputMsg({message: `Contact ${newPerson.name} could not be updated.`, success: false})
      clearOutputMsg()
    }
  }

  const addPerson = async (event) => {
    event.preventDefault()
    // Alert if no name or number entered
    if (newPerson.name === undefined || newPerson.name === '' || newPerson.number === undefined || newPerson.number === '') {
      setOutputMsg({message: `Please fill out form before submitting.`, success: false})
      clearOutputMsg()
    }
    // update person's number if name is already stored
    else if ( persons.map(p => p.name).includes(newPerson.name) ) {
      updatePerson(persons, newPerson)
    }
    // Otherwise add new contact to backend
    else {
      const personObj = {
        name: newPerson.name,
        number: newPerson.number
      }
      try {
        const savedPerson = await personService.addContact(personObj)
        setPersons(persons.concat(savedPerson))
        setOutputMsg({message: `Contact ${newPerson.name} successfully added.`, success: true})
        clearOutputMsg()
        setNewPerson({name: '', number: ''})
      } catch (err) {
        const { error } = err.response.data
        console.error(error)
        setOutputMsg({message: `Contact ${newPerson.name} could not be added. ${error ? error : ''}`, success: false})
        clearOutputMsg()
      }
    }
  }

  const handleNameChange = (event) => {
    setNewPerson({...newPerson, name: event.target.value})
  }
  const handleNumberChange = (event) => {
    setNewPerson({...newPerson, number: event.target.value})
  }
  
  return (
    <form onSubmit={addPerson}>
    <div>
      <div>Name: <input value={newPerson.name} onChange={handleNameChange} /></div>
      <div>Number: <input value={newPerson.number} onChange={handleNumberChange} /></div>
    </div>
    <div>
      <button type="submit">add</button>
    </div>
  </form>
  )
}

export default PersonForm;