import React from 'react'
import Button from './Button'
import personService from '../services/person-service'

const Persons = ({persons, setPersons, setOutputMsg, clearOutputMsg}) => {

  const deletePerson = async (person) => {
    const userConfirmedDelete = window.confirm(`Delete ${person.name}?`)
    if (! userConfirmedDelete) {
      return
    }
    try {
      console.log(person)
      await personService.deleteContact(person.id)
      setPersons(persons.filter(p => p.id !== person.id))
      setOutputMsg({message: `Contact ${person.name} deleted.`, success: true})
      clearOutputMsg()
    } catch (err) {
      console.error(err)
    }
  }

  return (
    <div>
    {persons.map(person => 
      <p key={person.id}>{person.name} {person.number} <Button text={'delete'} onClick={ () => deletePerson(person) } /></p>
    )}
    </div>
  )
}

export default Persons