import React, { useState, useEffect } from 'react'
import Persons from './components/Persons'
import PersonForm from './components/PersonForm'
import Filter from "./components/Filter"
import personService from './services/person-service'
import Notification from './components/Notification'


const App = () => {
  const [ persons, setPersons ] = useState([])
  const [ newPerson, setNewPerson ] = useState('')
  const [filterVal, setFilterVal] = useState('')
  const [outputMsg, setOutputMsg] = useState({message: null, success: null})

  const clearOutputMsg = () => {
    setTimeout(() => {
      setOutputMsg({message: '', success: null})
    }, 6000)
  }

  const fetchAll = async () => {
    setPersons(await personService.getAll())
  }
  
  // Use effect hook to fetch initial data from server
  useEffect(() => {
    fetchAll()
  }, [])

  // Filter persons based on filter input box value (unless value is empty string)
  const personsToShow = filterVal === '' ? persons :
    persons.filter(p => 
      p.name.toLowerCase().includes(filterVal.toLowerCase())
  )

  return (
    <div className='App'>
      <h1>Phonebook</h1>
      
      <h4>Add New Contact</h4>
      
      <PersonForm
        newPerson={newPerson}
        setNewPerson={setNewPerson}
        setPersons={setPersons}
        persons={persons}
        setOutputMsg={setOutputMsg}
        clearOutputMsg={clearOutputMsg}
      />
      
      <Notification data={outputMsg}/>

      <h4>Contacts List</h4>
      
      <Filter
        filterVal={filterVal}
        setFilterVal={setFilterVal}
      />
      
      <Persons
        persons={personsToShow}
        setPersons={setPersons}
        setOutputMsg={setOutputMsg}
        clearOutputMsg={clearOutputMsg}
      />
    </div>
  )
}

export default App
